<template>
  <div class="report">
    <app-nav></app-nav>

    <b-row><b-col>&nbsp;</b-col></b-row>

    <h2>Post-Event Report<span v-if="report.eventName"> for {{ report.eventName }}</span></h2>

    <b-row>
      <b-col cols="6" offset="3">
        <b-card v-bind:header="report.eventName">
        
          Some quick example text to build on the card title and make up the bulk of the card's content.

    
    </b-card>
      </b-col>
    </b-row>

    <b-row>
      <b-col class="text-center">
        <b-btn class="btn btn-info" @click="saveReport()">Save</b-btn>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import AppNav from "./AppNav";
import { getReport } from "../utils/conf-api";

export default {
  name: "report",
  components: { AppNav },
  data() {
    return {
      report: {},
    };
  },
  mounted() {
    this.getReport();
  },
  methods: {
    getReport() {
      console.log(this.$route.params);
      if (this.$route.params.reportId) {
        getReport(this.$route.params.reportId).then((report) => {
        console.log(report);
         this.report = {...report}
        });
      }
    },
  }
};
</script>

<style scoped>

</style>



